<template>
  <el-container>
    <el-aside width="200px">
      <el-row class="tac">
        <el-col :span="12">
          <el-menu
              default-active="2"
              class="el-menu-vertical-demo"
              @open="handleOpen"
              @close="handleClose">
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>数据配置</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="1-0" @click="Home()">首页</el-menu-item>
                <el-menu-item index="1-1" @click="UserList()">用户列表</el-menu-item>
                <el-menu-item index="1-3" @click="AddUserData()">注册用户</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title">文件操作</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="2-1" @click="FileList()">文件列表</el-menu-item>
                <el-menu-item index="2-2" @click="UpLoadFile()">文件上传</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-menu-item index="3" disabled>
              <i class="el-icon-document"></i>
              <span slot="title">导航三</span>
            </el-menu-item>
            <el-menu-item index="4" @click="ThisUserData()">
              <i class="el-icon-setting"></i>
              我的用户
            </el-menu-item>
          </el-menu>
        </el-col>
      </el-row>
    </el-aside>
    <el-drawer
        title="用户设置"
        :before-close="handleCloses"
        :visible.sync="drawer"
        direction="rtl"
        custom-class="demo-drawer"
        ref="drawer"
        style="text-align: center"
    >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <span style="text-align: left">账号:</span>
          <el-input v-model="account" type="text" disabled="true"></el-input>
          <br>
          <span style="text-align: left">密码:</span>
          <el-input v-model="form.password" type="text"></el-input>

        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForms">取 消</el-button>
          <el-button type="primary" @click="UserDataR()">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        </div>
        <br>
        <br>
        <span style="text-align: center">修改头像</span>
        <div class="demo-drawer__footer">
          <el-upload
              class="upload-demo"
              ref="upload"
              action="http://93rf36tz6467.vicp.fun:15427/SetUserDataImg"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :file-list="fileList"
              :auto-upload="false"
              :data="{account:account}"
          >
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            <el-button type="primary" @click="handleCloses()" :loading="this.loading">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
          </el-upload>
        </div>
      </div>
    </el-drawer>
    <el-container>



      <el-header>
        <!--固定图片-->
        <img src="http://93rf36tz6467.vicp.fun:15427/1111.jpg" height="80px">
         <div style="float:right">
           <!--用户设置的图片头像-->
           <div>
             <el-avatar :src="this.show" :alt="this.img"></el-avatar>

             <el-row class="block-col-2" style="float:right;" >
             <el-col :span="12">
               <el-dropdown @command="showSettion">
                <span class="el-dropdown-link">
                  {{this.account   }}<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                 <el-dropdown-menu slot="dropdown" >
                   <el-dropdown-item icon="el-icon-plus" command="1">黄金糕</el-dropdown-item>
                   <el-dropdown-item icon="el-icon-circle-plus" command="2">狮子头</el-dropdown-item>
                   <el-dropdown-item icon="el-icon-circle-plus-outline" command="3">螺蛳粉</el-dropdown-item>
                   <el-dropdown-item icon="el-icon-setting" command="User">用户设置</el-dropdown-item>
                   <el-dropdown-item icon="el-icon-circle-check" command="4">蚵仔煎</el-dropdown-item>
                 </el-dropdown-menu>
               </el-dropdown>
             </el-col>
             </el-row>
           </div>
          </div>

      </el-header>

      <el-main >
       <router-view></router-view>
      </el-main>

      <el-footer>

      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
import axios from "axios";
export default {
  name: 'NavMenuView',
  components: {},
  data() {
      return {
        drawer: false,
        dialog: false,
        loading: false,
        account:window.sessionStorage["account"],
        img:'',
        show:"http://93rf36tz6467.vicp.fun:15427/getThisImg?account="+window.sessionStorage['account'],
        form: {
          password:window.sessionStorage["password"],

        },
      }
  },
  methods: {
    UpLoadFile() {
      this.$router.push({path:'/UpLoad'})
    },
    Home(){
      this.$router.push({path:'/NavMenuView'})
    },
    UserList(){
      this.$router.push({path:'/UserData'})
    },
    FileList(){
      this.$router.push({path:'/FileData'})
    },
    AddUserData(){
      this.$router.push({path:'/AddUserData'})
    },
    ThisUserData(){
      this.$router.push({path:'/ThisUserDataUI'})
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    showSettion(command){
      if (command == 1){
        console.log(command);
      }else if (command == 2){
        console.log(command);
      }else if (command == 3){
        console.log(command);
      }else if (command == "User") {
        this.drawer = true
      }else if (command == 5){
        console.log(command);
      }
    },
    handleCloses() {
      this.loading=true
      this.submitUpload()
      this.cancelForm()
    },
    UserDataR(){
      axios.get("http://93rf36tz6467.vicp.fun:15427/SetUserData?account="+window.sessionStorage['account']+"&password="+this.form.password).then(r=>{
       if (r.data){
         this.$message({
           message: '修改成功',
           type: 'success'
         });
       }else {
         this.$message.error('修改失败');
       }
       this.drawer=false
      })
    },
    cancelForm() {
      this.loading = false;
      this.drawer = false;
    },
    cancelForms() {
      this.drawer = false;
    },
    submitUpload() {
      this.$refs.upload.submit();
      this.$message({
          message: '修改成功',
          type: 'success'
      });
    },
    gerImgData(){
      axios.get("http://93rf36tz6467.vicp.fun:15427/getThisImg?account="+window.sessionStorage["account"]).then(res => {
        this.img=res.data;
      })
    }
  },

  mounted() {
    if (window.sessionStorage["account"] == null){
      this.$router.push({path:'/'})
    }else {
      this.gerImgData()
    }
  }

}
</script>
<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>